@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  font-family: Quicksand, sans-serif;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@font-face {
  font-family: StreetCred;
  src: url("/static/media/Street Cred.662b97d6.ttf");
}

.style-box-1 {
  background-color: #282c34;
  height: 100vh;
  width: 120%;
  transform: skewY(-5deg) translateY(-12vh) scaleY(1);
}

.style-box-2 {
  background-color: #282c34;
  height: 100vh;
  width: 80%;
  transform: skewY(10deg) translateY(-14vh) scaleY(1.3);
}

.landing-container {
  padding: 50px 100px;
  position: absolute;
  color: white;
  font-family: 'Quicksand', sans-serif;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: calc(100% - 200px); /* 100% - 2*padding-horizontal */
  height: 80vh;
  overflow-x: hidden;
}

@media screen and (max-width: 500px) {
  .landing-container {
    padding: 50px 0;
    transform: scale(0.6);
    overflow: visible;
  }

  .terminal {
    visibility: hidden;
  }

  .style-box-2 {
    height: 95vh;
  }
}

h1 {
  font-family: StreetCred, monospace;
  font-size: 6rem;
  margin-bottom: 14px;
}

p {
  margin: 12px auto;
}

.horizontal-line {
  width: 0;
  height: 0.1pt;
  margin: 0 10px;
  transition-timing-function: ease;
  transition-delay: 100ms;
  transition-duration: 600ms;
}

.terminal {
  position: fixed;
  height: 60vh;
  width: 80vh;
  display: flex;
  flex-direction: column;
  transform: translateX(0%);
  transition-duration: 300ms;
  transition-timing-function: ease;
  right: 0;
  overflow: hidden;
  box-shadow: 0 0 20px #212121;
  border-radius: 10px 0 0 10px;
  z-index: 100;
}

.terminal .title-bar {
  justify-content: left;
  align-items: center;
  padding: 14px;
  background-color: #1e2743;
  display: flex;
}

.terminal .body {
  font-size: 16px;
  font-family: monospace;
  background-color: rgba(17, 21, 21, 0.6);
  height: 100%;
  border: 2px solid #1c2530;
  border-top: none;
  border-right: none;
  border-radius: 0 0 0 10px;
  -webkit-backdrop-filter: blur(6px);
          backdrop-filter: blur(6px);
  text-align: left;
  padding: 10px;
  overflow-y: scroll;
}

.terminal .body .sub-body {
  transition: 500ms opacity, 500ms visibility;
}

.terminal .body::-webkit-scrollbar {
  display: none;
}

input[type='text'] {
  background: transparent;
  border: none;
  outline: none;
  color: white;
  font-family: monospace;
  font-size: 16px;
  width: 90%;
}

.skill-cell {
  border-radius: 20px;
  margin: 3px;
  padding: 2px 8px;
  font-size: 16px;
  font-weight: 500;
  border: 2pt solid cyan;
  opacity: 0;
  transition-duration: 200ms;
}

.skill-cell:hover {
  border-color: white;
}

button {
  font-family: Quicksand, sans-serif;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  outline: none;
}

.theme0 {
  border-radius: 50px;
  border: 2.3pt solid #fff;
  background-color: transparent;
  color: white;
  font-size: 18px;
  transition-duration: 200ms;
  transition-timing-function: ease;
  padding: 10px 20px;
  justify-content: center;
}

.theme0:hover {
  border: 2.3pt solid #fff;
  color: white;
  padding-left: 40px;
  padding-right: 40px;
}

.theme1 {
  border-radius: 10px;
  border: 1pt solid lightgrey;
  color: dodgerblue;
  font-size: 18px;
  transition-duration: 200ms;
  transition-timing-function: ease;
  padding: 10px 25px;
  justify-content: center;
}

.theme1:hover {
  border: 1pt solid dodgerblue;
}

.ringed-circle {
  border: 3pt solid #282c34;
  border-radius: 50%;
  height: 6px;
  width: 6px;
}

.dot {
  border-radius: 50%;
  height: 10px;
  width: 10px;
  background-color: #282c34;
}

h2 {
  font-weight: 900;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  padding-left: 10px;
  padding-right: 10px;
}


.footer-container {
  margin-top: 50px;
}

.footer-decorator-top {
  height: 20px;
  background-color: #282c34;
  width: 30%;
  transform: skewX(30deg) translateX(-15px);
}

.footer {
  padding: 30px;
  background-color: #282c34;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  color: white;
  font-weight: 500;
  flex-wrap: wrap;
}

.experience-container {
  padding: 0 14% 50px;
}

.experience-container .sub-container {
  color: black;
}

@-webkit-keyframes ripple {
  0% {
    padding: 0;
    opacity: 1;
    border-width: 0;
  }

  5% {
    border-width: 2px;
  }

  100% {
    padding: 30px;
    border-color: transparent;
  }
}

@keyframes ripple {
  0% {
    padding: 0;
    opacity: 1;
    border-width: 0;
  }

  5% {
    border-width: 2px;
  }

  100% {
    padding: 30px;
    border-color: transparent;
  }
}

.rippling-border {
  -webkit-animation: ripple 2s infinite;
          animation: ripple 2s infinite;
  position: absolute;
}

.required-skills > * {
  margin-right: 5px;
  margin-top: 5px;
}

body {
  overflow-x: hidden;
}

#laptop-mockup-container {
  width: 100%;
  height: 42vw;
  display: flex;
  justify-content: center;
  align-items: center;
  padding-top: 50px;
  padding-bottom: 50px;
  transition-duration: 300ms;
}

#mobile-mockup-container {
  width: 100%;
  height: 42vw;
  display: flex;
  justify-content: center;
  align-items: center;
  padding-top: 50px;
  padding-bottom: 50px;
  transition-duration: 300ms;
  position: absolute;
}

#laptop-mockup {
  position: absolute;
  width: 80vw;
  height: 45vw;
}

#mobile-mockup {
  position: absolute;
  width: 42vh;
  height: 82vh;
}

#laptop-content {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: flex-start;
  justify-content: center;
  width: 60.6vw;
  height: 37vw;
  transform: translateY(-1.3vw) translateX(0.5pt);
  overflow-y: scroll;
  transition-duration: 1000ms;
  -webkit-filter: brightness(0%);
          filter: brightness(0%);
  background-color: black;
}

#laptop-content::-webkit-scrollbar {
  display: none;
}

#mobile-content {
  width: 100%;
  border-bottom-left-radius: 3.2vh;
  border-bottom-right-radius: 3.2vh;
  margin-bottom: 3px;
}

#mobile-content::-webkit-scrollbar {
  display: none;
}

#laptop-content .tiles {
  display: flex;
  flex-direction: column;
  height: 33%;
  width: calc(60.6vw / 3 - 2px);
  justify-content: center;
  align-items: center;
  color: white;
  font-weight: 700;
  margin: 1px;
  z-index: 10;
  transition-duration: 200ms;
  -webkit-filter: grayscale(0%);
          filter: grayscale(0%);
}

#mobile-content .tiles {
  display: flex;
  flex-direction: column;
  height: 15%;
  width: 100%;
  padding-left: 20px;
  justify-content: center;
  align-items: flex-start;
  color: white;
  font-size: 1.25rem;
  font-weight: 700;
  transition-duration: 200ms;
  -webkit-filter: grayscale(0%);
          filter: grayscale(0%);
}

#laptop-content .tiles:hover {
  -webkit-filter: grayscale(40%);
          filter: grayscale(40%);
}

#mobile-content .tiles:hover {
  -webkit-filter: grayscale(40%);
          filter: grayscale(40%);
}


